@import url('https://fonts.googleapis.com/css2?family=Archivo:wght@100;300;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes enter {
	0% {
		opacity: 0;
		transform: translateY(10px);
	}

	to {
		opacity: 1;
		transform: none;
	}
}

.safari-blur {
	-webkit-backdrop-filter: blur(10px);
}

.gradient-rust-text {
	background: linear-gradient(270deg, #b9481b 6.16%, #dea584 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.gradient-typescript-text {
	background: linear-gradient(270deg, #1f4878 6.16%, #3277c6 100%);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.gradient-underline {
	background: linear-gradient(
		90deg,
		#ffffff 0%,
		rgba(255, 255, 255, 0) 129.11%
	);
}

.gradient-text {
	background: linear-gradient(
		320deg,
		rgba(255, 255, 255, 0.1) 50%,
		#ffffff 100%
	);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

[data-animate] {
	--stagger: 0;
	--delay: 120ms;
	--start: 0ms;
}

@media (prefers-reduced-motion: no-preference) {
	[data-animate] {
		animation: enter 0.6s both;
		animation-delay: calc(var(--stagger) * var(--delay) + var(--start));
	}
}

[data-animation-controller='false'] [data-animate] {
	animation: none;
}

.slide-enter-content > * {
	--stagger: 0;
	--delay: 150ms;
	--start: 0ms;
	animation: enter 1s both 1;
	animation-delay: calc(var(--start) + var(--stagger) * var(--delay));
}

